<template>
    <div class="spbox">
        <van-loading v-if="newdata == ''" class="loading" type="spinner" color="#aa8c5d"></van-loading>
        <van-pull-refresh v-model="isLoading" @refresh="onRefresh" pulling-text="" loosing-text=""
            loading-text="">
            <!-- <p>: {{ count }}</p> -->
            <NewsList v-show="newdata" v-for="news in newdata" :key="news.news_id" :news="news" />
        </van-pull-refresh>
    </div>
</template>

<script>
// import { mapState } from "vuex";
import NewsList from "@/components/NewsList.vue";
import qs from "qs";
import Vue from 'vue';
import { Toast } from 'vant'
import { Loading } from 'vant';
import { PullRefresh } from 'vant';
Vue.use(Toast);
Vue.use(PullRefresh);
Vue.use(Loading);
export default {
    components: {
        NewsList,
    },
    data: function () {
        return {
            newdata: [],
            isLoading: false,
        }
    },
    computed: {
        // ...mapState(["count"]),
    },
    methods: {
        onRefresh() {
            setTimeout(() => {
                Toast('');
                this.isLoading = false;
            }, 1000);
        },
    },
    created() {
        this.axios({
            url: "https://sonydpj.ccwonline.com/getvideolist",
            method: "POST",
            data: qs.stringify({
                video_id: "0",
            }),
            headers: { "content-type": "application/x-www-form-urlencoded" },
        }).then((res) => {
            // console.log(res.data.video_list);
            this.newdata = res.data.video_list
        });
    },
};
</script>

<style lang="scss" scoped>
.spbox {
    width: 100vw;
    height: 100%;
    background: #2e2e31;

    .loading {
        width: 100vw;
        height: 100vh;
        padding-top: 15px;
        text-align: center;
    }
}
</style>