<template>
  <div class="hdbox">
    <van-loading v-if="activ == ''" class="loading" type="spinner" color="#aa8c5d"></van-loading>
    <van-pull-refresh v-model="isLoading" @refresh="onRefresh" pulling-text="" loosing-text=""
      loading-text="">
      <ActivList v-for="hdlist in activ " :key="hdlist.activity_id" :hdlist="hdlist" />
    </van-pull-refresh>
  </div>
</template>

<script>
import ActivList from "@/components/ActivList.vue";

import Vue from 'vue';
import { Toast } from 'vant'   //
import { Loading } from 'vant';
import { PullRefresh } from 'vant';
Vue.use(Toast);
Vue.use(PullRefresh);
Vue.use(Loading);
export default {
  components: {
    ActivList,
  },
  data: function () {
    return {
      activ: [],
      isLoading: false,
    }
  },
  created() {
    this.axios.post("https://sonydpj.ccwonline.com/getactivitylist").then((res) => {
      // console.log(res.data);
      this.activ = res.data.activity_list
    });
  },
  methods: {
    showPopup() {
      this.show = true;
    },
    onRefresh() {
      setTimeout(() => {
        Toast('');
        this.isLoading = false;
      }, 1000);
    },
  },
};
</script>

<style lang="scss" scoped>
.hdbox {
  width: 100%;
  height: 100%;
  background: #2e2e31;
  text-align: center;

  .loading {
    width: 100vw;
    height: 100vh;
    padding-top: 15px;
    text-align: center;
  }

}
</style>