import Vue from 'vue'
import VueRouter from 'vue-router'  //
import HomeView from '../views/HomeView.vue'
import HomeNavView from '../views/HomeNavView.vue'
import CaseView from '../views/CaseView.vue'
import ProductView from '../views/ProductView.vue'
import ActivityView from '../views/ActivityView.vue'
import DownloadView from '../views/DownloadView.vue'
import ActiqxView from '../views/ActiqxView.vue'


import CaseAllView from '../views/CaseAllView.vue' //
import CasebraView from '../views/CasebraView.vue'
import CasecryView from '../views/CasecryView.vue'
import CasegcView from '../views/CasegcView.vue'
import CasesjView from '../views/CasesjView.vue'
import CasejyView from '../views/CasejyView.vue'

import DownloadZwcyView from '../views/DownloadZwcyView.vue' //
import DownloadTyrjView from '../views/DownloadTyrjView.vue'
import DownloadSpView from '../views/DownloadSpView.vue'


import ZwcybraView from '../views/ZwcybraView.vue'  //
import ZwcycryView from '../views/ZwcycryView.vue'
import ZwcygcView from '../views/ZwcygcView.vue'
import ZwcysjView from '../views/ZwcysjView.vue'
import ZwcyjyView from '../views/ZwcyjyView.vue'
import ZwcyxnfzView from '../views/ZwcyxnfzView.vue'
import ZwcyyxzView from '../views/ZwcyyxzView.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: '',
    component: HomeNavView,
    children: [
      {
        path: '',
        name: 'home',
        component: HomeView
      },
      {
        path: '/news',
        name: 'news',
        // route level code-splitting
        // this generates a separate chunk (about.[hash].js) for this route
        // which is lazy-loaded when the route is visited.
        component: () => import(/* webpackChunkName: "about" */ '../views/NewsView.vue')
      },
      {
        path: '/case',
        name: 'case',
        component: CaseView,
        redirect: '/case/all',
        children: [
          {
            path: '/case/all',
            name: 'all',
            component: CaseAllView,
          },
          {
            path: '/case/bra',
            name: 'bra',
            component: CasebraView
          },
          {
            path: '/case/cry',
            name: 'cry',
            component: CasecryView
          },
          {
            path: '/case/gc',
            name: 'gc',
            component: CasegcView
          },
          {
            path: '/case/sj',
            name: 'sj',
            component: CasesjView
          },
          {
            path: '/case/jy',
            name: 'jy',
            component: CasejyView
          },
        ]
      },
      {
        path: '/product',
        name: 'product',
        component: ProductView,
        redirect: '/product/bra',
        children: [
          {
            path: '/product/bra',
            // name: 'bra',
            component: CasebraView
          },
          {
            path: '/product/cry',
            // name: 'cry',
            component: CasecryView
          },
          {
            path: '/product/gc',
            // name: 'gc',
            component: CasegcView
          },
          {
            path: '/product/sj',
            // name: 'sj',
            component: CasesjView
          },
          {
            path: '/product/jy',
            // name: 'jy',
            component: CasejyView
          },
        ]
      },
      {
        path: '/activity',
        name: 'activity',
        component: ActivityView
      },
      {
        path: '/download',
        name: 'download',
        component: DownloadView,
        redirect: '/download/zwcy',
        children: [
          {
            path: '/download/zwcy',
            name: 'zwcy',
            component: DownloadZwcyView,
            redirect: '/download/zwcy/bra',
            children: [
              {
                path: '/download/zwcy/bra',
                // name: 'bra',
                component: ZwcybraView,
              },
              {
                path: '/download/zwcy/cry',
                // name: 'cry',
                component: ZwcycryView,
              },
              {
                path: '/download/zwcy/gc',
                // name: 'gc',
                component: ZwcygcView,
              },
              {
                path: '/download/zwcy/sj',
                // name: 'sj',
                component: ZwcysjView,
              },
              {
                path: '/download/zwcy/jy',
                // name: 'jy',
                component: ZwcyjyView,
              },
              {
                path: '/download/zwcy/xnfz',
                // name: 'xnfz',
                component: ZwcyxnfzView,
              },
              {
                path: '/download/zwcy/yxz',
                // name: 'yxz',
                component: ZwcyyxzView,
              },
            ]
          },
          {
            path: '/download/tyrj',
            name: 'tyrj',
            component: DownloadTyrjView,
          },
          {
            path: '/download/sp',
            name: 'sp',
            component: DownloadSpView,
          },
        ]
      },
    ],
  },
  {
    path: '/actiqx',
    name: 'actiqx',
    component: ActiqxView,
  },

]

const router = new VueRouter({
  routes
})

export default router
