<template>
    <div class="casebox">
        <van-loading v-if="alAll == ''" class="loading" type="spinner" color="#aa8c5d"></van-loading>
        <van-pull-refresh v-model="refreshing" @refresh="onRefresh" pulling-text="" loosing-text=""
            loading-text="">
            <van-list v-model="loading" :style="{ height: (this.alAll.length > 20 ? '3780' + 'rem' : '1770' + 'rem') }"
                class="vanList" :finished="finished" finished-text="" offset="50" @load="onLoad">
                <CaseList v-for=" item in alAll" :key="item.case_id" :item="item" />
            </van-list>
        </van-pull-refresh>
    </div>
</template>

<script>
import qs from "qs";
import CaseList from "@/components/CaseList.vue";

import Vue from 'vue';
import { Cell, List, Toast, Loading, PullRefresh } from 'vant'
Vue.use(Cell, Toast, Loading, PullRefresh,)
Vue.use(List)
export default {
    components: {
        CaseList,
    },
    data: function () {
        return {
            alAll: [],
            loading: false,
            finished: false,  //
            refreshing: false,
            index: 0,   // 
        }
    },
    created() {
        this.axios({
            url: "https://sonydpj.ccwonline.com/getcaselist",
            method: "POST",
            data: qs.stringify({
                case_id: "0",
                type: "0",
            }),
            headers: { "content-type": "application/x-www-form-urlencoded" },
        }).then((res) => {
            // console.log(res.data.case_list);
            this.alAll = res.data.case_list
        });

    },
    methods: {
        onLoad() {
            setTimeout(() => {
                if (this.refreshing) {
                    // this.alAll = [];
                    this.refreshing = false;
                }
                this.loading = false;

            }, 1000);

            if (this.loading == true) {
                this.finished = true;
                if (this.index == 0) {
                    this.axios({  //bra
                        url: "https://sonydpj.ccwonline.com/getcaselist",
                        method: "POST",
                        data: qs.stringify({
                            case_id: "829",
                            type: "0",
                        }),
                        headers: { "content-type": "application/x-www-form-urlencoded" },
                    }).then((res) => {
                        // console.log(res.data.case_list);
                        for (let i = 0; i < res.data.case_list.length; i++) {
                            // this.alAll.push(this.alAll.length + 1);
                            this.alAll.push(res.data.case_list[i])
                        }
                    })
                    this.index++;
                }
            }

        },
        onRefresh() {
            // 
            this.finished = false;
            // 
            //  loading  true
            this.onLoad();
            setTimeout(() => {
                Toast('');
                this.isLoading = false;
                this.loading = false;
            }, 1500);
        },
    },

}
</script>

