<template>
    <div class="casejy">
        <van-loading v-if="cpjy == ''" class="loading" type="spinner" color="#aa8c5d"></van-loading>
        <van-pull-refresh v-model="refreshing" @refresh="onRefresh" pulling-text="" loosing-text=""
            loading-text="">
            <div class="cplis" v-if="this.$route.matched[1].path === '/product'">
                <CpList v-for="item in cpjy" :key="item.product_id" :item="item" />
            </div>
            <div class="caselis" v-if="this.$route.matched[1].path === '/case'">
                <van-list v-model="loading" class="vanList" :finished="finished" finished-text="" offset="50"
                    @load="onLoad">
                    <CaseList v-for=" item in aljy" :key="item.case_id" :item="item" />
                </van-list>
            </div>
        </van-pull-refresh>
    </div>
</template>
<script>
import qs from "qs";
import CpList from "@/components/CpList.vue";
import CaseList from "@/components/CaseList.vue";

import Vue from 'vue';
import { Cell, List, Toast, Loading, PullRefresh } from 'vant'
Vue.use(Cell, Toast, Loading, PullRefresh,)
Vue.use(List)

export default {
    components: {
        CpList,
        CaseList
    },
    data: function () {
        return {
            cpjy: [],
            aljy: [],
            loading: false,
            finished: false,  //
            refreshing: false,
        }
    },
    created() {
        this.axios({
            url: "https://sonydpj.ccwonline.com/getproductlist",
            method: "POST",
            data: qs.stringify({
                type: "3",
                product_id: "0",
                screen_key: '{"":[],"":[]}',
            }),
            headers: { "content-type": "application/x-www-form-urlencoded" },
        }).then((res) => {
            // console.log(res.data.product_list);
            this.cpjy = res.data.product_list
        });
        this.axios({
            url: "https://sonydpj.ccwonline.com/getcaselist",
            method: "POST",
            data: qs.stringify({
                case_id: "0",
                type: "3",
            }),
            headers: { "content-type": "application/x-www-form-urlencoded" },
        }).then((res) => {
            // console.log(res.data.case_list);
            this.aljy = res.data.case_list
        });
    },
    methods: {
        onLoad() {
            setTimeout(() => {
                if (this.refreshing) {
                    // this.albra = [];
                    this.refreshing = false;
                }
                this.loading = false;

                if (this.aljy.length >= 11) {
                    this.finished = true;
                }
            }, 1000);
        },
        onRefresh() {
            // 
            this.finished = false;
            // 
            //  loading  true
            this.onLoad();
            setTimeout(() => {
                Toast('');
                this.loading = false;
            }, 1000);
        },
    },
}
</script>

