.casejy {
    width: 100vw;
    height: 100%;

    .caselis {
        width: calc(100% - 3rem);
        height: 100%;
        margin-top: 3rem;
        margin-left: 3rem;
        background: #fff;
    }

    .cplis {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        align-content: flex-start;
        background: #2e2e31;
    }

    .vanList {
        display: flex;
        flex-direction: column;
        height: 1166rem;
        flex-wrap: wrap;
    }

    :deep(.van-list__finished-text) {
        position: relative;
        left: 84px;
    }

    .loading {
        width: 100vw;
        height: 100vh;
        padding-top: 15px;
        text-align: center;
    }
}
</style>