<template>
    <div class="casesj">
        <van-loading v-if="cpsj == ''" class="loading" type="spinner" color="#aa8c5d"></van-loading>
        <van-pull-refresh v-model="refreshing" @refresh="onRefresh" pulling-text="" loosing-text=""
            loading-text="">
            <div class="cplis" v-if="this.$route.matched[1].path === '/product'">
                <CpList v-for="item in cpsj" :key="item.product_id" :item="item" />
            </div>
            <div class="caselis" v-if="this.$route.matched[1].path === '/case'">
                <van-list v-model="loading" class="vanList"
                    :style="{ height: (this.alsj.length > 20 ? '2708' + 'rem' : '2110' + 'rem') }" :finished="finished"
                    finished-text="" @load="onLoad" offset="50">
                    <CaseList v-for=" item in alsj" :key="item.case_id" :item="item" />
                </van-list>
            </div>
        </van-pull-refresh>
    </div>
</template>

<script>
import qs from "qs";
import CpList from "@/components/CpList.vue";
import CaseList from "@/components/CaseList.vue";

import Vue from 'vue';
import { Cell, List, Toast, Loading, PullRefresh } from 'vant'
Vue.use(Cell, Toast, Loading, PullRefresh,)
Vue.use(List)
export default {
    components: {
        CpList,
        CaseList
    },
    data: function () {
        return {
            cpsj: [],
            alsj: [],
            loading: false,
            finished: false,  //
            refreshing: false,
            index: 0,   // 
        }
    },
    created() {
        this.axios({
            url: "https://sonydpj.ccwonline.com/getproductlist",
            method: "POST",
            data: qs.stringify({
                type: "2",
                product_id: "0",
                screen_key: '{"":[],"":[]}',
            }),
            headers: { "content-type": "application/x-www-form-urlencoded" },
        }).then((res) => {
            // console.log(res.data.product_list);
            this.cpsj = res.data.product_list
        });
        this.axios({
            url: "https://sonydpj.ccwonline.com/getcaselist",
            method: "POST",
            data: qs.stringify({
                case_id: "0",
                type: "2",
            }),
            headers: { "content-type": "application/x-www-form-urlencoded" },
        }).then((res) => {
            // console.log(res.data.case_list);
            this.alsj = res.data.case_list
        });
    },
    methods: {
        onLoad() {
            setTimeout(() => {
                if (this.refreshing) {
                    this.refreshing = false;
                }
                this.loading = false;

            }, 1000);

            if (this.loading == true) {
                this.finished = true;
                if (this.index == 0) {
                    this.axios({  //bra
                        url: "/getcaselist",
                        method: "POST",
                        data: qs.stringify({
                            case_id: "300",
                            type: "2",
                        }),
                        headers: { "content-type": "application/x-www-form-urlencoded" },
                    }).then((res) => {
                        // console.log(res.data.case_list);
                        for (let i = 0; i < res.data.case_list.length; i++) {
                            // this.alAll.push(this.alAll.length + 1);
                            this.alsj.push(res.data.case_list[i])
                        }
                    })
                    this.index++;
                }
            }

        },
        onRefresh() {
            // 
            this.finished = false;
            // 
            //  loading  true
            this.onLoad();
            setTimeout(() => {
                Toast('');
                this.isLoading = false;
                this.loading = false;
            }, 1500);
        },
    },
}
</script>

<style lang="scss" scoped>
.casesj {
    width: 100vw;
    height: 100%;


    .caselis {
        width: calc(100% - 3rem);
        height: 100%;
        margin-top: 3rem;
        margin-left: 3rem;
        background: #fff;
    }

    .cplis {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        align-content: flex-start;
        background: #2e2e31;
    }

    .vanList {
        display: flex;
        flex-direction: column;
        // height: 2008rem;
        // height: 3860rem;
        flex-wrap: wrap;
    }

    .loading {
        width: 100vw;
        height: 100vh;
        padding-top: 15rem;
        text-align: center;
    }

    :deep(.van-list__finished-text) {
        position: relative;
        left: 84px;
    }
}
</style>