<script>
import Vue from 'vue';
import qs from 'qs';
import KeyWordList from "@/components/KeyWordList.vue";
import { Toast, Popup, Search } from 'vant';

Vue.use(Search);
Vue.use(Popup);
Vue.use(Toast);
export default {
    components: {
        KeyWordList,
    },
    data: function () {
        return {
            title: {
                "home": '',
                "news": '',
                "case": '',
                "product": '',
                "activity": '',
                "download": '',
            },
            show: false,
            showtwo: false,
            value: '',
            keyword: null,
            search: null,
        }
    },
    methods: {
        searchView() {
            this.show = true;
        },
        gd() {
            this.showtwo = true;
        },
        onCancel() {
            //
            this.show = false;
            this.search = '';
        },
        onSearch(val) {
            console.log(val);
            this.keyword = val;
            this.axios({
                url: "https://sonydpj.ccwonline.com/getsearchtype",
                method: "POST",
                data: qs.stringify({
                    category: "0",
                    keyword: this.keyword,
                }),
                headers: { "content-type": "application/x-www-form-urlencoded" },
            }).then((res) => {
                console.log(res.data.search_list);
                this.search = res.data.search_list;
            });


        },
    },
}
</script>

<style lang="scss">
:deep(.van-search) {
    background-color: #000;
}

.keyword {
