    bottom: 0;
    left: 0;
    // font-size: 12px;
    width: 100vw;
    display: flex;
    justify-content: space-around;
    box-sizing: border-box;
    padding: 8px 0;
    background: #000;
    z-index: 9;

    a {
        color: #fff;
        display: flex;
        flex-direction: column;
        align-items: center;
        font-size: 12rem;

        &.router-link-active {
            color: #d1a45a;

            .iconC {
                fill: currentColor;
                color: #d1a45a;

            }
        }

        svg {
            padding-bottom: 6px;
        }

    }
}

.appnavbox {
    height: 40rem;
    width: 100vw;
    position: relative;

    .appnav {
        background: #000;
        height: 40rem;
        width: 100vw;
        font-size: 26rem;
        padding: 8rem 10rem;
        box-sizing: border-box;
        color: #fff;
        display: flex;
        justify-content: space-between;
        align-items: center;
        position: fixed;
        top: 0;
        left: 0;
        z-index: 99;

        .texttitle {
            font-size: 18rem;
            flex: 1;
            text-align: center;
        }

        &::before {
            display: block;
            content: '';
            height: 40rem;
            width: 0.01rem;
        }
    }
}
