<template>
    <div class="zwcybox">
        <van-loading v-if="zwcybra == ''" class="loading" type="spinner" color="#aa8c5d"></van-loading>
        <van-pull-refresh v-model="isLoading" @refresh="onRefresh" pulling-text="" loosing-text=""
            loading-text="">
            <ZwcyList v-for="item in zwcybra" :key="item.book_id" :item="item" />
        </van-pull-refresh>
    </div>
</template>

<script>
import ZwcyList from "@/components/ZwcyList.vue";

import qs from "qs";
import Vue from 'vue';
import { Popup } from 'vant';   // 
Vue.use(Popup);

import { Toast } from 'vant'   //
import { Loading } from 'vant';
import { PullRefresh } from 'vant';
Vue.use(Toast);
Vue.use(PullRefresh);
Vue.use(Loading);
export default {
    components: {
        ZwcyList,
    },
    data: function () {
        return {
            zwcybra: [],
            isLoading: false,
        }
    },
    created() {
        this.axios({  //bra
            url: "https://sonydpj.ccwonline.com/getproductbooklist",
            method: "POST",
            data: qs.stringify({
                type: "5",
                book_id: "0",
            }),
            headers: { "content-type": "application/x-www-form-urlencoded" },
        }).then((res) => {
            // console.log(res.data.book_list);
            this.zwcybra = res.data.book_list
        });
    },
    methods: {
        onRefresh() {
            setTimeout(() => {
                Toast('');
                this.isLoading = false;
            }, 1000);
        },
    },
}
</script>

<style lang="scss" scoped>
.zwcybox {
    .loading {
        padding-top: 15rem;
        text-align: center;
    }

    :deep(.van-pull-refresh__track) {
        height: calc(100vh - 114rem);
    }
}
</style>