<template>
  <div class="home">
    <!-- <HelloWorld v-for="item in banner" :key="item.id" :banner="banner" /> -->
    <van-loading v-if="banner == ''" class="loading" type="spinner" color="#aa8c5d"></van-loading>
    <van-pull-refresh v-model="isLoading" @refresh="onRefresh" pulling-text="" loosing-text=""
      loading-text="">
      <van-swipe :autoplay="3000" :height="256" indicator-color="#aa8c5d">
        <van-swipe-item v-for="(banner, index) in banner" :key="index">
          <a :href="`https://sonydpj.ccwonline.com/share_read?category=1&detailid=${banner.banner_id}`"><img
              v-lazy="banner.banner_image_url" style="width:100%;height: 100%;" /></a>
        </van-swipe-item>
      </van-swipe>
      <div class="homeimg" v-for="home in home" :key="home.id">
        <a :href="`https://sonydpj.ccwonline.com/share_read?category=2&detailid=${home.home_id}`"><img
            :src="home.home_image_url" alt=""></a>
      </div>
    </van-pull-refresh>
  </div>
</template>

<script>
import Vue from 'vue';
import { Swipe, SwipeItem } from 'vant';
import { Lazyload } from 'vant';
import { Toast } from 'vant'
import { Loading } from 'vant';
import { PullRefresh } from 'vant';
Vue.use(Toast);
Vue.use(PullRefresh);
Vue.use(Loading);
Vue.use(Lazyload);
Vue.use(Swipe);
Vue.use(SwipeItem);
// import HelloWorld from "@/components/HelloWorld.vue";
export default {
  name: "HomeView",
  components: {
    // HelloWorld,
  },
  data: function () {
    return {
      banner: [],
      home: [],
      isLoading: false,
    }
  },
  created() {
    this.axios.post("https://sonydpj.ccwonline.com/gethome").then((res) => {
      // console.log(res.data);
      this.banner = res.data.banner_list
      this.home = res.data.home_list
    });
  },
  methods: {
    onRefresh() {
      setTimeout(() => {
        Toast('');
        this.isLoading = false;
        // this.count++;
      }, 1000);
    },
  },
};
</script>
