<template>
    <div class="tyrjbox">
        <van-loading v-if="rjdata == ''" class="loading" type="spinner" color="#aa8c5d"></van-loading>
        <van-pull-refresh v-model="isLoading" @refresh="onRefresh" pulling-text="" loosing-text=""
            loading-text="">
            <div class="databox">
                <AppList v-for="tyrj in rjdata" :key="tyrj.app_id" :tyrj="tyrj" />
            </div>
        </van-pull-refresh>

    </div>
</template>

<script>
import AppList from "@/components/AppList.vue";
import Vue from 'vue';
import { Popup } from 'vant';   // 
Vue.use(Popup);

import { Toast } from 'vant'   //
import { Loading } from 'vant';
import { PullRefresh } from 'vant';
Vue.use(Toast);
Vue.use(PullRefresh);
Vue.use(Loading);

export default {
    components: {
        AppList,
    },
    data: function () {
        return {
            rjdata: [],
            isLoading: false,
        }
    },
    created() {
        this.axios.post("https://sonydpj.ccwonline.com/getapplist").then((res) => {
            console.log(res.data);
            this.rjdata = res.data.app_list
        });
    },
    methods: {
        onRefresh() {
            setTimeout(() => {
                Toast('');
                this.isLoading = false;
            }, 1000);
        },
    },
}
</script>

<style lang="scss" scoped>
.tyrjbox {
    width: 100vw;
    font-size: 12rem;

    .databox {
        width: 100vw;
        height: calc(100vh - 82rem);
        display: flex;
        justify-content: space-between;

    }

    .loading {
        padding-top: 15rem;
        text-align: center;
    }


}
</style>